import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { SelectItem, Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../../http/product-manage.http.service";
import { ClassificationApplicationTypeBean } from './../bean/classification-application-type.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-classification-application-type-add',
  templateUrl: './classification-application-type-add.component.html',
  styleUrls: ['./classification-application-type-add.component.scss'],
  providers: [ProductManageHttpService, Commfunc]
})
/**
 * 分类应用类型-新增
 */
export class ClassificationApplicationTypeAddComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  // bean
  classificationApplicationTypeBean: ClassificationApplicationTypeBean = new ClassificationApplicationTypeBean();
  //提示信息
  msgs: Message[] = [];
  userform: FormGroup; //校验
  siteType: any;//站点类型
  code: any;
  iscompele: boolean = false;

  constructor(public productManageHttpService: ProductManageHttpService,
    public fb: FormBuilder,
    public commfunc: Commfunc, ) {
    this.userform = fb.group({
      'prodCatalogCategoryTypeId': new FormControl('', [Validators.pattern(/^[A-Za-z0-9]+$/), Validators.required]),
      // channelName: new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
      'description': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
    });
    this.codeValues(); // 调用方法，获取全部码值
    this.siteType = this.code['SiteType'];
  }

  ngOnInit() {

  }
  // 码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }

  // 添加
  addSaveBtn() {
    this.iscompele = true;
    // 验证
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        this.iscompele = false;
        return;
      }
    }
    // 提交服务
    const param = {
      parentTypeId: 'TZ',
      prodCatalogCategoryTypeId: this.classificationApplicationTypeBean.prodCatalogCategoryTypeId,
      description: this.classificationApplicationTypeBean.description,
    }
    this.productManageHttpService.addProdCatalogCategoryType(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.classificationApplicationTypeBean = new ClassificationApplicationTypeBean();
        let parm = {
          display: false,
          code: data.returnCode.message
        }
        this.outValue.emit(parm);
        this.iscompele = false;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
        this.iscompele = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.iscompele = false;
    });
  }
  // 取消
  addCloseBtn() {
    let parm = {
      display: false,
      code: 'cancelModel'
    };
    this.outValue.emit(parm);
  }

}

