import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { ConfirmationService } from 'primeng/primeng';//删除
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { AttributevaluetypeBean } from './bean/attribute-value-type.bean';
@Component({
  selector: 'app-attribute-value-type',
  templateUrl: './attribute-value-type.component.html',
  styleUrls: ['./attribute-value-type.component.scss'],
  providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * 类型管理-产品类型
 */
export class AttributevaluetypeComponent implements OnInit {
  tabMesg: any = tableMessage;//列表无数据
  // bean
  AttributevaluetypeBean: AttributevaluetypeBean = new AttributevaluetypeBean();
  //修改的参数
  updateValue: any = [];
  //提示信息
  msgs: Message[] = [];
  //总页数
  total: string;
  first: number = 0;//分页控制
  //模态框
  showModel: string;
  display: boolean = false;
  //模态框的标题
  headerTitle: string;
  // 列表
  site_list: any = [];//渠道列表

  constructor(public productManageHttpService: ProductManageHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc) { }

  ngOnInit() {
    this.queryClick();
  }

  // 重置
  resetClick() {
    this.AttributevaluetypeBean = new AttributevaluetypeBean()
  }

  //查询始终第一页
  querySecle() {
    this.AttributevaluetypeBean.pageSize = 10;
    this.AttributevaluetypeBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  //  查询
  queryClick() {
    this.AttributevaluetypeBean.channelCode = this.AttributevaluetypeBean.channelId;
    this.productManageHttpService.queryProductFeatureValueTypeList(this.AttributevaluetypeBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.site_list = data.resultList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //删除
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          productFeatureValueTypeId: item.productFeatureValueTypeId,
        };
        this.productManageHttpService.delProductFeatureValueType(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //新增
  addClick() {
    this.display = true;
    this.headerTitle = '新增';
    this.showModel = '1';
  }
  //新增的回调
  addCall(param) {
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }
  // 修改按钮
  modifyClick(item) {
    this.display = true;
    this.headerTitle = '修改';
    this.showModel = '2';
    this.updateValue = item;
  }
  //修改的回调
  updateCall(param) {
    if (param.code == 'cancelModel') {
      this.display = param.display;
    } else {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
      this.display = param.display;
    }
    this.first = 0;
    this.queryClick();
  }
  //分页
  paginate(event) {
    // ;
    //每页显示的条数
    this.AttributevaluetypeBean.pageSize = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.AttributevaluetypeBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.AttributevaluetypeBean.pageSize;
    //调用查询的方法
    this.queryClick();
    this.AttributevaluetypeBean.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
