import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { ClassificationTypeComponent } from './classification-type/classification-type.component';
import { ClassificationApplicationTypeComponent } from './classification-application-type/classification-application-type.component';
import { ProductTypeComponent } from './product-type/product-type.component';
import { PropertyLevelComponent } from './property-level/property-level.component';
import { AttributevaluetypeComponent } from './attribute-value-type/attribute-value-type.component';
import { ProductDeliveryComponent } from './product-delivery/product-delivery.component';

const routes: Routes = [
    {
        path: 'classification-type', component: ClassificationTypeComponent,
    },
    {
        path: 'classification-application-type', component: ClassificationApplicationTypeComponent,
    },
    {
        path: 'product-type', component: ProductTypeComponent,
    },
    {
        path: 'property-level', component: PropertyLevelComponent,
    },
    {
        path: 'attribute-value-type', component: AttributevaluetypeComponent,
    },
    {
        path: 'product-delivery', component: ProductDeliveryComponent,
    },
];

export const TypeManagementRouting: ModuleWithProviders = RouterModule.forChild(routes);